          <p>Der systemische Ansatz betrachtet den einzelnen Menschen in seinen Beziehungen und seinem Umfeld. Der Blick richtet sich sowohl auf das was in einem Menschen vorgeht als auch darauf, wie Menschen ihre Beziehung zueinander gestalten, wie sie miteinander umgehen, wie sie aufeinander reagieren und wie sich dadurch Muster gefestigt haben. Es werden Regeln und Muster betrachtet, die sich im Laufe der Zeit in Beziehungen herausgebildet haben und der Therapeut/ die Therapeutin überlegt gemeinsam mit dem Klienten, ob diese noch nützlich und hilfreich sind.</p>
